#portfolio {
	margin-top: 1.3rem;
	.col {
		display: flex;
		justify-content: center; }
	.proj {
		display: block;
		width: 52.4rem;
		height: 35.5rem;
		background: center center;
		background-size: cover;
		box-shadow: 0px 0px 2.5px rgba(0, 0, 0, 0.08);
		border-radius: 0.3px;
		position: relative;
		overflow: hidden;
		cursor: pointer;

		&:hover .popup {
			right: 0;
			opacity: 1; }

		.popup {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			right: -100%;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			color: white;
			padding: 6.1rem 3.5rem 1.8rem;
			background: #191919;
			opacity: 0;
			transition: right 0.4s, opacity 0.3s;


			&__title {
				font-size: 2.4rem;
				line-height: 4rem;
				margin-bottom: 1.6rem;
				@include font-wide;
				flex: 1 0; }

			&__descr {
				font-size: 2.2rem;
				line-height: 4rem;
				flex: 2 0; }

			&__link {
				color: white;
				width: max-content;
				align-self: flex-end;
				font-size: 2rem;
				line-height: 4rem;
				display: flex;
				align-items: center;
				text-transform: uppercase;
				@include medium;
				font-weight: 500;
				flex: 1 0;

				&:hover {
					text-decoration: underline; }
				&::after {
					content: '';
					width: 2.4rem;
					height: 2.4rem;
					transform: translate(1.6rem, -0.3rem);
					background: url('../img/link-icon.svg'); } } } } }
