header {
	z-index: 1;
	@media screen and (max-width: 876px) {
		font-size: 1.4rem; }
	@media screen and (max-width: 805px) {
		font-size: 1.3rem; }

	.col { //the one with the text
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		z-index: 2;
		@media screen and (max-width: $xl) {
			padding-left: 3em;
			padding-right: 1em; }

		&:last-child { // column with the burger image
			justify-content: flex-start;
			z-index: 1; } }
	a {
		width: max-content;
		font-size: 2.4rem;
		color: $blue;
		text-decoration: underline; }

	.checkout {
		width: max-content;
		position: absolute;
		top: 2em; }
	.wrap {
		position: relative; }

	h1.title {
		font-size: 9.2rem;
		margin-bottom: 3.6rem;
		line-height: 9.2rem;
		&:after {
			background: url('../img/header-head.png') center center;
			min-width: 2em;
			min-height: 2em; } }
	p {
		max-width: 80rem;
		font-size: 2.2rem;
		line-height: 4rem; } }
