#cv {
	margin-top: 12.1rem;
	margin-bottom: 5rem;
	h1.title {
		font-size: 4.5rem;
		line-height: 4.5rem;
		margin-bottom: 2.93rem;
		color: $blue;
		&:after {
			background: url('../img/skills-head.svg') center center;
			min-width: 2em;
			min-height: 2em; } }


	.glide {
		height: 36rem;
		display: flex;
		align-items: center;
		justify-content: center;

		&__track {
			height: 100%;
			width: 100%; } //only when diabled
		&__slides {
			height: 100%; }
		&__slide {
			.col, .row {
				height: 100%; }
			.col {
				display: flex;
				align-items: center; } }
		&__arrow {
			box-shadow: none;
			padding: 0;
			&--left {
				left: -5rem;
				img {
					transform: rotate(180deg); } }
			&--right {
				right: -5rem; } } }

	.slider {
		&__text-block {
			color: #000000;
			font-size: 2.2rem;
			line-height: 4.5rem;
			max-width: 41.1rem;
			margin: 0;
			padding: 0; }
		&__border-spec {
			border: 1.5rem solid transparent;
			border-image: url('../img/square.svg') 30 round;
			border-image-outset: 0.8rem; }
		&__skill-tags {
			width: 100%;
			max-width: 52.7rem;
			align-self: center; }
		&__skill-bagdes {
			width: 100%;
			height: 100%;
			padding: 0 2.5rem;
			align-items: center; }
		&__skill-progres-bar {
			padding: 0 1.5rem;
			display: flex;
			flex-direction: column;
			justify-content: center; } } }


